<template>

<Loading v-if="!hasInitialized" class="h-screen"/>

<!-- component -->
<div class="flex flex-col justify-center container w-full md:max-w-3xl mx-auto pt-20" v-else>
  <div class="mx-auto md:w-full md:max-w-md">
    <h1 class="font-bold text-center text-2xl mb-5">Login First</h1>  
    <div class="bg-white shadow w-full rounded-lg divide-y divide-gray-200">
      <form class="px-5 py-7" @submit.prevent="submit">

        <div class="rounded text-sm text-red-600 border border-red-600 bg-red-100 p-2 mb-5" v-if="errors.length > 0">
          {{ errors }}
        </div>

        <label class="font-semibold text-sm text-gray-600 pb-1 block">E-mail</label>
        <input type="email" class="border rounded-lg px-3 py-2 mt-1 mb-5 text-sm w-full" v-model="email" :disabled="isLoading" required="" />
        <label class="font-semibold text-sm text-gray-600 pb-1 block">Password</label>
        <input type="password" class="border rounded-lg px-3 py-2 mt-1 mb-5 text-sm w-full" v-model="password" :disabled="isLoading" required="" />

        <button type="submit" class="transition duration-200 bg-green-500 hover:bg-green-600 focus:bg-green-700 focus:shadow-sm focus:ring-4 focus:ring-green-500 focus:ring-opacity-50 text-white w-full py-2.5 rounded-lg text-sm shadow-sm hover:shadow-md font-semibold text-center inline-block">
            <span class="inline-block mr-2">
              {{ !isLoading ? 'Login' : 'Logging in' }}
            </span>

            <i class="fa fa-spinner text-md inline-block spin" v-if="isLoading"></i>
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-4 h-4 inline-block" v-else>
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
        </button>
      </form>

      <!--
      <div class="p-5">
          <div class="grid grid-cols-3 gap-1">
              <button type="button" class="transition duration-200 border border-gray-200 text-gray-500 w-full py-2.5 rounded-lg text-sm shadow-sm hover:shadow-md font-normal text-center inline-block">MailUp</button>
              <button type="button" class="transition duration-200 border border-gray-200 text-gray-500 w-full py-2.5 rounded-lg text-sm shadow-sm hover:shadow-md font-normal text-center inline-block">Google</button>
              <button type="button" class="transition duration-200 border border-gray-200 text-gray-500 w-full py-2.5 rounded-lg text-sm shadow-sm hover:shadow-md font-normal text-center inline-block">Github</button>
          </div>
      </div>
      -->
        <div class="py-5">
        <div class="grid grid-cols-2 gap-1">
          <div class="text-center sm:text-left whitespace-nowrap">
            <button class="transition duration-200 mx-5 px-5 py-4 cursor-pointer font-normal text-sm rounded-lg text-gray-500 hover:bg-gray-100 focus:outline-none focus:bg-gray-200 focus:ring-2 focus:ring-gray-400 focus:ring-opacity-50 ring-inset">
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-4 h-4 inline-block align-text-top">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 11V7a4 4 0 118 0m-4 8v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2z" />
                </svg>
                <span class="inline-block ml-1" @click="signInWithPopup">Forgot Password</span>
            </button>
          </div>
          <div class="text-center sm:text-right whitespace-nowrap">
            <button class="transition duration-200 mx-5 px-5 py-4 cursor-pointer font-normal text-sm rounded-lg text-gray-500 hover:bg-gray-100 focus:outline-none focus:bg-gray-200 focus:ring-2 focus:ring-gray-400 focus:ring-opacity-50 ring-inset">
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-4 h-4 inline-block align-text-bottom ">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M18.364 5.636l-3.536 3.536m0 5.656l3.536 3.536M9.172 9.172L5.636 5.636m3.536 9.192l-3.536 3.536M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-5 0a4 4 0 11-8 0 4 4 0 018 0z" />
                </svg>
                <span class="inline-block ml-1">Help</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

</template>

<script>

import { ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import Loading from '@/components/Loading.vue'
import { auth, firebase } from '@/firebase'
import { useAuthBinder } from '@/mixins'

window.auth = auth
window.firebase = firebase

export default {

  components: { Loading },

  setup() {
    const store = useStore()
    const router = useRouter()

    const { hasInitialized, hasUser, userChanged } = useAuthBinder()

    const email = ref('')
    const password = ref('')
    const errors = ref('')

    const isLoading = ref(false)

    //Route Guard
    if (hasInitialized.value && hasUser.value) {
      router.push({name: 'AdminHome'})
    }

    userChanged((hasUser) => {
      if (hasUser) router.push({name: 'AdminHome'})
    })

    const submit = async () => {
      try {

        if (isLoading.value) return

        isLoading.value = true
        errors.value = ''

        await store.dispatch('login', {
          email: email.value, 
          password: password.value
        })

      } catch (err) {
        errors.value = 'Wrong email or password'
      } finally {
        isLoading.value = false
      }

    }

    const signInWithPopup = () => {
      var provider = new firebase.auth.GoogleAuthProvider();
      auth.signInWithPopup(provider)
    }

    return { email, password, submit, isLoading, errors, hasInitialized,signInWithPopup }

  }
}

</script>